/*
  This file forces the fallback to CSS2 era code so that (dompdf)[https://github.com/dompdf/dompdf]
  can render a PDF of the webpage.

  NOTE: Some of the strange code like prefixing with div is necessary.
 */

body {
  background-image: none !important;
  background-color: #fff;
  margin: 0;
  padding: 3em 4em;
  font-size: 10pt;
}

nav {
  display: none;
}

div.level {
  //border: 1px solid red;
  display: block;
  max-width: 100vw;

  // Clearfix
  &:after {
    clear: both;
  }
}

div.level-left,
div.level-right {
  min-width: 200px;
}

div.level-left {
  float: left;
}

div.level-right {
  float: right;
}

div.has-text-right {
  text-align: right;
}

table.invoice-items {
  margin-top: 1.2em;
}

tr.balance-row {
  background-color: #eeecf7;
  border-top: 2px solid #654ab3;
}

.is-hidden-print {
  display: none;
}